/* HTML elements */
h1{
	font-size: 2rem;
	font-weight: 300;
}

a{
	color: inherit;
	text-decoration: none;
}

p{
	line-height: $line-height-base;
}

/* Display settings */
.block{
	display: block;
}
.inline-block{
	display: inline-block;
}
.hidden{
	display: none;
}
.table{
	display: table;

	&.full-width{
		width: 100%;
	}

	.table-cell{
		display: table-cell;
	}

	.table-row{
		display: table-row;
	}

	.table-column{
		display: table-column;
	}
}
.no-wrap, .nowrap{
	white-space: nowrap;
}

.normal-wrap, .wrap{
	white-space: normal;
}

/* List */
ul, ol{
	&.plain{
		padding: 0;
		margin: 0;
		list-style: none;
	}
}

/* Positioning */
.pull-left{
	float: left;
}
.pull-right{
	float: right;
}
.relative, .rel{
	position: relative;
}
.absolute, .abs{
	position: absolute;
}
.fixed{
	position: fixed;
}
.static{
	position: static;
}
.center-block{
	margin-left: auto !important;
	margin-right: auto !important;
}
.v-middle{
	vertical-align: middle;
}
.v-top{
	vertical-align: top;
}
.v-bottom{
	vertical-align: bottom;
}
.center-unknown{
	text-align: center;
	white-space: nowrap;

	&:before{
		content: '';
		display: inline-block;
		height: 100%;
		vertical-align: middle;
		margin-right: -0.25em;
	}

	> *{
		white-space: normal;
		display: inline-block;
		vertical-align: middle;
	}
}

/* Sizes */
.half{
	width: 50%;
}
.full-width, .fill-width{
	width: 100%;
}
.inner-width, .content-width{
	max-width: 1400px;
}
.thin-width{
	max-width: 800px;
	margin-right: auto;
	margin-left: auto;
}
.font-zero{
	font-size: 0;
	
	> *{
		font-size: 1rem;
	}
}
img.responsive{
	max-width: 100%;
	height: auto;
}
.no-margin{
	margin: 0;
}
.no-padding{
	padding: 0;
}

/* Font Styles */
.italic{
	font-style: italic;
}
.bold{
	font-weight: bold;
}
.underline{
	text-decoration: underline;
}
.uppercase{
	text-transform: uppercase;
}
.lowercase{
	text-transform: lowercase;
}
.normalcase{
	text-transform: none;
}
.no-decor{
	text-decoration: none;
}
.raleway, .sans-serif, .sans{
	font-family: $raleway;
}
.neutron, .serif{
	font-family: $neutron;
}
.right{
	text-align: right;
}
.left{
	text-align: left;
}
.center{
	text-align: center;
}
.justify{
	text-align: justify
}

/* Font sizes */
.font-s{
	font-size: $s;
}
.font-m{
	font-size: $m;
}
.font-l{
	font-size: $l;
}
.font-xl{
	font-size: $xl;
}
.font-xxl{
	font-size: $xxl;
}
.font-xxxl{
	font-size: $xxxl;
}
.font-xxxxl{
	font-size: $xxxxl;
}
.font-xxxxxl{
	font-size: $xxxxxl;
}

/* Font Colours */
.white{
	color: $white;
}
.black{
	color: $black;
}
.light-grey, .grey{
	color: $light-grey;
}
.green{
	color: $green;
}
.red{
	color: $red;
}

/* Background Colours */
.bg-white{
	background-color: $white;
}
.bg-black{
	background-color: $black;
}
.bg-light-grey, .bg-grey{
	background-color: $light-grey;
}
.bg-green{
	background-color: $green;
}
.bg-red{
	background-color: $red;
}

/* Cursors */
.cursor{

	&.pointer{
		cursor: pointer;
	}
	&.arrow{
		cursor: default;
	}
}


/* Image properties */
img{
	.responsive-width{
		max-width: 100%;
		height: auto;
	}
}
a img{
	border: none;
}

/* Fix mobile scrolling */
.fix-touch-scroll{
	overflow-y: scroll;
	-webkit-overflow-scrolling: touch;
}

/* Buttons */
.button{
	display: inline-block;
	background-color: $light-grey;
	color: $black;
	text-align: center;
	padding: 8px 25px;
	cursor: pointer;
	text-decoration: none;
	letter-spacing: 1px;

	&:hover{
		background-color: lighten($light-grey, 10);
	}

	&.green-button{
		background-color: $green;
		color: $white;
		&:hover{
			background-color: lighten($green, 10);
		}
	}
	&.red-button{
		background-color: $red;
		color: $white;
		&:hover{
			background-color: lighten($red, 10);
		}
	}
}

/* Form elemenst */
input, textarea, select{
	box-sizing: border-box;
	width: 100%;
	font-family: $raleway;
	padding: 5px;
	appearance: none;
	border: 2px solid;
	font-size: 1rem;
	background: transparent;
	border-radius: 0;
	color: $black;

	&:focus, &:active{
		outline: none;
	}

	&.invert{
		color: $white;
	}
}

/** Video platyers */
.video-wrapper{
	position: relative;
	padding-bottom: 56.25%;
	padding-top: 25px;
	height: 0;

	> .video-container iframe{
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
	}
}

/** Placeholder colours (via mixin) **/
@include placeholder-color("input", $black);
@include placeholder-color("textarea", $black);
@include placeholder-color("input.invert", $white);
@include placeholder-color("textarea.invert", $white);

input[type='submit']{
	width: auto;
	cursor: pointer;

	&:hover{
		background: rgba(0, 0, 0, 0.1);
	}
}

textarea{
	min-height: 5.8rem;
}

select{
}
/* Arrows for select tags (only on webkit browsers) */
@media screen and (-webkit-min-device-pixel-ratio:0) {
	select{
		background-position: calc( 100% - 7px) center;
		background-repeat: no-repeat;
		background-image: url('data:image/svg+xml;utf8;base64,');
		background-size: 18px auto;
	}
}
@media all and (max-width: 992px){
	.block-image{
		min-height: 450px;

		&.match-height{
			position: relative;
		    left: initial;
		    right: initial;
		    bottom: initial;
		    top: initial;
		    background-size: cover;
		    background-position: center;
		    width: 100%;
		    display: block;
		}
	}
}