html,
body {
   margin:0;
   padding:0;
   height:100%;
}

html{
	font-size: $font-size-base;
}
body{
	font-family: $neutron;
	background-color: #fff;
	font-size: 1rem;
	letter-spacing: 1px;

	&#tinymce{
		margin: 10px;
	}
}

// Font smoothing
* {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

#seo-data-bar{
	position: fixed;
	top: 0;
	left: 0;
	right: 0;
	background-color: $green;
	z-index: 2;
	padding: 10px;

	> a{
		@extend .button;
		@extend .uppercase;
		font-family: $raleway;
		font-size: 0.8rem;
		font-weight: bold;
		color: $black;
	}

	+ #wrapper{
		padding-top: 51px;
		min-height: calc(100% - 51px);
	}
}

#wrapper{
	min-height:100%;
	position:relative;

	> #container{
		padding:10px;
		padding-bottom:60px;
	}

   > #header{

		#header-logo{
			padding-right: 40px;
			padding-left: 20px;

			img{
				width: 212px;
				height: auto;
			}
		}
		#header-nav{
			> ul{

				>li{
					width: 25%;

					&:hover{
						background-color: rgba(255, 255, 255, 0.1);
					}

					a{
						padding: 20px 20px;
						display: block;
					}

					&#header-search-btn{
						background-color: $red;
						color: $white;
						white-space: nowrap;

						&:hover{
							background-color: lighten($red, 10);
						}

						> a {
							display: inline-block;
							padding: 20px 40px;

							> span{

								&:first-child{
									margin-right: 15px;
								}
								&:last-child{
									vertical-align: middle;
									vertical-align: -webkit-baseline-middle;
									font-size: 1.3rem;
								}
							}
						}
					}
				}
			}
		}
   }

   > #footer{
		position:absolute;
		bottom:0;
		width:100%;
   }
}	